<template>
	<div class="detail">
		<header :style="{background:mainColor}" class="header">
			<van-icon name="arrow-left" @click="$router.back()" />
			<p>文章详情</p>
		</header>
		<div class="w">
			<h2 class="title">{{item.title}}</h2>
			<div class="author">
				<div class="left">
					<img :src="avatar" alt="">
					<p>{{item.author}}</p>
					<br>
					<span>{{new Date(Number(item.time)).toLocaleDateString()}}</span>
				</div>
				<div class="right" @click="attention">
					<van-button round type="default" size="small">
						<van-icon name="plus" v-show="isAttention" />
						{{isAttention?"已关注":"关注"}}
					</van-button>
				</div>
			</div>
			<div class="text">
				{{text}}
			</div>
			<div v-show="item.imageSrc" class="photo">
				<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" :key="i">
			</div>
		</div>
		<div class="comment">
			<ul>
				<li v-for="item in commentList" :key="item._id">
					<img :src="item.info.avatar" alt="">
					<div class="content">
						<p class="username">{{item.info.nickname}}</p>
						<p class="con" @click="yesShow(item)">{{item.content}}</p>
						<p class="time">{{new Date(item.create_time).toLocaleDateString()}} <span
								@click="getReplyList(item)">{{item.reply_num}}
								回复</span></p>
						<div v-show="clickItem._id==item._id">
							<van-loading size="24px" type="spinner" v-show="isShow">加载中...</van-loading>
						</div>
						<ul v-show="clickItem._id==item._id" style="width: 120%;margin-left: -44px;">
							<li v-for="item in replyList" :key="item._id">
								<img :src="item.info.avatar" alt="">
								<div class="content">
									<p class="username">{{item.info.nickname}}</p>
									<p class="con">{{item.content}}</p>
									<p class="time">{{new Date(item.create_time).toLocaleDateString()}} <span
											@click="second">{{item.reply_num}}
											回复</span></p>
								</div>
								<div class="nice_count">
									<van-icon name="good-job" v-show="item.is_like" @click="commentNice(item)"
										color="#ef092c" />
									<van-icon name="good-job-o" v-show="!item.is_like" @click="commentNice(item)" />
									<span>{{item.like_count}}</span>
								</div>
							</li>
						</ul>
					</div>
					<div class="nice_count">
						<van-icon name="good-job" v-show="item.is_like" @click="commentNice(item)" color="#ef092c" />
						<van-icon name="good-job-o" v-show="!item.is_like" @click="commentNice(item)" />
						<span>{{item.like_count}}</span>
					</div>
				</li>
			</ul>
		</div>
		<van-popup v-model="show">
			<van-field v-model="msg" ref="msgIpt" @keyup.enter="reviewArticle(clickItem,1)" placeholder="请输入你的要回复的内容" />
			<div class="btns">
				<van-button type="default" size="small" @click="noShow">取消</van-button>
				<van-button type="primary" size="small" @click="reviewArticle(clickItem,1)">确认</van-button>
			</div>
		</van-popup>
		<footer class="footerHandle">
			<input type="text" v-model.trim="commentText" placeholder="写评论" @keyup.enter="reviewArticle(null,0)">
			<van-icon name="comment-o" @click="reviewArticle" />
			<van-icon name="star-o" v-show="!isCollect" @click="collect" />
			<van-icon name="star" v-show="isCollect" color="#0078d4" @click="collect" />
			<van-icon name="like-o" v-show="!isLike" @click="like" />
			<van-icon name="like" v-show="isLike" color="#ef092c" @click="like" />
			<van-icon name="share-o" />
		</footer>
	</div>
</template>

<script>
	import axios from "axios"
	import {
		mapState
	} from "vuex";
	import {
		Toast,
		// ShareSheet
		Loading
	} from 'vant';
	export default {
		props: ['item'],
		name: 'detail',
		data() {
			return {
				show: false,
				text: '',
				msg: '',
				isCollect: false,
				isLike: false,
				commentList: [],
				avatar: '',
				commentText: '',
				clickItem: '',
				replyList: [],
				isShow: false,
				isNice: false,
				isAttention: false
			}
		},
		components: {
			// [ShareSheet.name]: ShareSheet
			[Loading.name]: Loading,
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		methods: {
			attention() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				this.isAttention = !this.isAttention;
				if (this.isAttention) {
					Toast({
						message: "关注成功",
						icon: 'success',
						duration: 1200,
					});
					return
				}
				Toast({
					message: "取消关注成功",
					icon: 'fail',
					duration: 1200,
				});
			},
			collect() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (this.isCollect) {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isCollect = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						console.log(res.data);
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isCollect = true;
							return
						}
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			like() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (this.isLike) {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							// console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isLike = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isLike = true;
							return
						}
						// console.log(res.data);
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			second() {
				Toast({
					message: '这里的区域以后再来探索吧',
					icon: 'like-o',
				});
			},
			commentNice(niceItem) {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (niceItem.is_like) {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike',
						data: {
							comment_id: niceItem._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							// console.log(res.data);
							if (!res.data.code) {
								this.comment();
								this.getReplyList(niceItem);
								console.log(1);
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like',
					data: {
						comment_id: niceItem._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						if (!res.data.code) {
							this.getReplyList(niceItem);
							this.comment();
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							return
						}
						// console.log(res.data);
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			noShow() {
				this.show = false;
				this.msg = "";
			},
			yesShow(comItem) {
				this.clickItem = comItem;
				this.show = true;
				this.$nextTick(() => {
					this.$refs.msgIpt.focus();
				})
			},
			getReplyList(commentItem) {
				console.log(commentItem);
				this.isShow = true
				this.clickItem = commentItem;
				let perId;
				if (this.person_info) {
					perId = this.person_info._id;
				}
				this.replyList = []
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list',
					data: {
						user_id: perId,
						"reply_comment_id": commentItem._id,
						"article_id": this.item._id,
					}
				}).then(
					response => {
						// console.log(response.data);
						this.isShow = false
						this.replyList = response.data.data
					}
				)
			},
			comment() {
				let perId;
				if (this.person_info) {
					perId = this.person_info._id;
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
					data: {
						"article_id": this.item._id,
						user_id: perId
					}
				}).then(
					res => {
						// console.log(res.data.data[0]._id);
						// console.log(this.item);
						this.commentList = res.data.data;
					}
				)
			},
			reviewArticle(commentItem, num) {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (commentItem) {
					if (!this.msg) {
						Toast.fail('请输入内容');
						this.$refs.msgIpt.focus();
						return
					}
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
					data: {
						user_id: this.person_info._id,
						"article_id": this.item._id,
						"content": this.show ? this.msg : this.commentText,
						"reply_comment_id": commentItem ? commentItem._id : '',
						"comment_type": num
					}
				}).then(
					res => {
						// console.log(res.data);
						if (!this.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.show = false;
							this.commentText = '';
							this.msg = '';
							this.comment();
							return
						}
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			}
		},
		mounted() {
			// console.log(this.item);
			let perId;
			if (this.person_info) {
				perId = this.person_info._id;
			}
			axios({
				method: 'post',
				url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
				data: {
					"article_id": this.item._id,
					user_id: perId
				}
			}).then(
				response => {
					// console.log(response.data);
					if (response.data.data) {
						this.avatar = response.data.data.avatar;
						this.isCollect = response.data.data.is_fav;
						this.isLike = response.data.data.is_like;
						if (response.data.data) {
							this.text = response.data.data.content;
						}
					}
				}
			)
			this.comment()
		}
	}
</script>

<style scoped lang="less">
	.detail {
		width: 100%;
		margin-top: 50px;

		.w {
			padding: 0 15px;
		}

		.title {
			margin: 20px;
		}

		.author {
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 60px;

			.left {
				// display: flex;
				width: 40%;
				flex: 1;

				img {
					float: left;
					width: 60px;
					height: 60px;
					border-radius: 50%;
					margin-top: 10px;
					margin-right: 10px;
				}

				p {
					padding-top: 12px;
				}
			}
		}

		.photo {

			img {
				width: 100%;
			}
		}
	}

	.text {
		padding: 20px 15px;
	}

	.van-popup {
		width: 80%;
		padding: 20px;
	}

	.btns {
		display: flex;
		justify-content: space-around;
		margin-top: 10px;
	}

	.comment {
		margin-bottom: 60px;
		margin-top: 20px;

		li {
			display: flex;
			justify-content: space-between;
			padding: 0 15px;
			margin: 14px 0;

			img {
				width: 50px;
				height: 50px;
			}

			.content {
				flex: 1;
				margin-left: 15px;

				.username {
					font-size: 14px;
					color: #7f9ecb;
				}

				.con {
					margin: 3px 0;
				}

				.time {
					font-size: 12px;

					span {
						display: inline-block;
						padding: 2px 6px;
						border: 1px solid #ccc;
						border-radius: 10px;
						margin-left: 5px;
					}
				}
			}
		}
	}
</style>
