<template>
	<div>
		<header :style="{background:mainColor}" class="header">
			<van-icon name="arrow-left" @click="$router.back()" />
			<p>我的{{info}}</p>
		</header>
		<ul class="list commonList">
			<li v-for="item in myPublishList" :key="item._id" @click="toDetail(item)">
				<div class="list_top">
					<p>{{item.title}}</p>
					<div v-show="item.imageSrc">
						<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" :key="i">
					</div>
				</div>
				<div class="list_bottom">
					<span>作者 {{item.author}}</span>
					<span>{{item.comment}} 评论</span>
					<span>发布日期：{{new Date(item.time).toLocaleDateString()}}
						{{new Date(item.time).toLocaleTimeString()}}</span>
					<span v-if="info=='发布'">
						<van-icon name="cross" @click.stop="removeThis(item)" />
					</span>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import axios from 'axios'
	import {
		mapState,
	} from 'vuex'
	import {
		Toast,
		Dialog
	} from 'vant';
	export default {
		data() {
			return {
				myPublishList: []
			}
		},
		methods: {
			removeThis(item) {
				// console.log(item, this.person_info);
				// console.log(item._id, this.person_info._id);
				Dialog.confirm({
						message: `确认删除 "${item.title}" 吗？`,
					})
					.then(() => {
						// on confirm
						axios({
							method: 'post',
							url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/del_user_article',
							data: {
								"_id": item._id,
								"uid": this.person_info._id
							}
						}).then(
							res => {
								if (!res.data.code) {
									this.getMyPub()
									setTimeout(() => {
										Toast.success(res.data.msg);
									}, 400)
								} else {
									Toast.fail(res.data.msg);
								}
							}
						)
					})
					.catch(() => {
						// on cancel
					});
			},
			getMyPub() {
				axios({
					method: 'get',
					url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.person_info._id}&skip&limit`
				}).then(
					res => {
						this.myPublishList = res.data.data;
					}
				)
			},
		},
		activated() {
			// console.log(this.info);
			if (this.info === '发布') {
				this.getMyPub()
			}
			if (this.info === '收藏') {
				axios({
					method: 'post',
					url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_fav_list`,
					data: {
						user_id: this.person_info._id
					}
				}).then(
					res => {
						this.myPublishList = res.data.data;
					}
				)
			}
			if (this.info === '历史') {
				axios({
					method: 'get',
					url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_history_list?uid=${this.person_info._id}&skip&limit`

				}).then(
					res => {
						this.myPublishList = res.data.data;
					}
				)
			}
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		props: ['info']
	}
</script>

