<template>
	<div class="update">
		<!-- 名字 -->
		<van-popup v-model="nameShow">
			<van-field v-model="newName" placeholder="请输入你的昵称" />
			<div class="btns">
				<van-button type="default" size="small" @click="nameShow=false">取消</van-button>
				<van-button type="primary" size="small" @click="getNewName">确认</van-button>
			</div>
		</van-popup>
		<!-- 性别 -->
		<van-popup v-model="sexShow">
			<van-radio-group v-model="newSex" direction="horizontal">
				<van-radio name="男">男</van-radio>
				<van-radio name="女">女</van-radio>
			</van-radio-group>
			<div class="btns">
				<van-button type="default" size="small" @click="sexShow=false">取消</van-button>
				<van-button type="primary" size="small" @click="getNewSex">确认</van-button>
			</div>
		</van-popup>
		<!-- 时间 -->
		<van-popup v-model="timeShow">
			<van-datetime-picker v-model="currentDate" type="date" title="选择年月日" :min-date="minDate" :max-date="maxDate"
				@confirm="confirm" @cancel="cancel" />
		</van-popup>
		<header :style="{background:mainColor}" class="header">
			<van-icon name="arrow-left" @click="$router.back()" />
			<p>编辑信息</p>
		</header>
		<!-- 头像 -->
		<div class="head">
			<van-cell title="头像" :icon="photo" is-link />
			<van-uploader :max-count="1" v-model="fileList" />
		</div>
		<!-- 昵称 -->
		<van-cell is-link @click="changeName">
			<!-- 使用 title 插槽来自定义标题 -->
			<template #title>
				<span>昵称</span>
				&nbsp;
				<span class="title">{{oldName}}</span>
			</template>
		</van-cell>
		<!-- 性别 -->
		<van-cell is-link @click="changeSex">
			<!-- 使用 title 插槽来自定义标题 -->
			<template #title>
				<span>性别</span>
				&nbsp;
				<span class="title">{{sex?sex:'未设置'}}</span>
			</template>
		</van-cell>
		<!-- 生日 -->
		<van-cell is-link @click="timeShow=true">
			<!-- 使用 title 插槽来自定义标题 -->
			<template #title>
				<span>生日</span>
				&nbsp;
				<span class="title">{{newTime?newTime:'未设置'}}</span>
			</template>
		</van-cell>
		<van-button type="primary" block style="margin: 20px auto;width: 80%;" @click="changePerson">确定</van-button>
	</div>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	import {
		Uploader,
		RadioGroup,
		Radio,
		DatetimePicker,
		Toast
	} from 'vant'
	import axios from 'axios';
	import {
		nanoid
	} from 'nanoid';
	export default {
		name: 'updateInfo',
		data() {
			return {
				fileList: [],
				nameShow: false,
				sexShow: false,
				timeShow: false,
				photo: '',
				newPhoto: '',
				oldName: '',
				newName: '',
				newSex: '',
				sex: '',
				newTime: '',
				minDate: new Date(1980, 0, 1),
				maxDate: new Date(2022, 10, 19),
				currentDate: '',
			}
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		methods: {
			changeName() {
				this.newName = this.oldName;
				this.nameShow = true;
			},
			getNewName() {
				this.oldName = this.newName;
				this.nameShow = false;
			},
			changeSex() {
				this.newSex = this.sex;
				this.sexShow = true;
			},
			getNewSex() {
				this.sex = this.newSex;
				this.sexShow = false;
			},
			confirm(value) {
				this.newTime = value.toLocaleDateString();
				this.timeShow = false;
			},
			cancel() {
				this.currentDate = new Date(this.newTime);
				this.timeShow = false;
			},
			changePerson() {
				// console.log(this.fileList[0].file);
				// return
				axios({
					method: 'get',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token'
				}).then(
					res => {
						console.log(res.data.token);
						// console.log(this.newPhoto);
						let FD = new FormData();
						FD.append('key', nanoid());
						FD.append('token', res.data.token)
						FD.append('file', this.fileList[0].file)

						axios({
							method: 'post',
							url: 'https://upload-z1.qiniup.com',
							data: FD
						}).then(
							res => {
								// console.log(res.data);
								let avatar = `http://toutiao.longxiaokj.com/${res.data.key}`;
								axios({
									method: 'post',
									url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/editUserInfo',
									data: {
										"uid": this.person_info._id,
										avatar, //头像
										"nickname": this.newName,
										"birthday": this.newTime,
										"sex": this.newSex
									}
								}).then(
									res => {
										// console.log(res.data);
										if (!res.data.code) {
											// console.log(res);
											this.$store.commit('aboutPerson/changeInfo', ['nickname', this
												.newName
											]);
											this.$store.commit('aboutPerson/changeInfo', ['avatar', avatar]);
											this.$store.commit('aboutPerson/changeInfo', ['birthday', this
												.newTime
											]);
											this.$store.commit('aboutPerson/changeInfo', ['sex', this.newSex]);
											Toast.success(res.data.msg);
											this.$router.back();
										} else {
											Toast.fail(res.data.msg);
										}
									}
								)
							}
						)
					}
				)

			}
		},
		components: {
			[Uploader.name]: Uploader,
			[RadioGroup.name]: RadioGroup,
			[Radio.name]: Radio,
			[DatetimePicker.name]: DatetimePicker
		},
		mounted() {
			this.photo = this.person_info.avatar;
			this.oldName = this.person_info.nickname;
			this.newName = this.person_info.nickname;
			this.sex = this.person_info.sex;
			this.newSex = this.person_info.sex;
			this.newTime = this.person_info.birthday;
			this.currentDate = new Date(this.person_info.birthday);
		}
	}
</script>

<style scoped lang="less">
	.update {
		margin-top: 50px;
	}

	.van-cell__left-icon {
		position: absolute;
		right: 40px;
		top: 14px;
	}

	.van-popup {
		width: 80%;
		padding: 20px 0;
	}

	.btns {
		display: flex;
		justify-content: space-around;
		margin-top: 10px;
	}

	.van-radio-group--horizontal {
		justify-content: space-evenly;
	}

	.van-icon__image {
		width: 3em;
		height: 2em;
	}

	.van-cell__left-icon[data-v-12fb4767] {
		top: 1.4vw;
	}
</style>
