<template>
	<div>
		<div class="fixTop">
			<header>
				<div class="search" @click="toSearch">
					<van-icon name="search" color="#fff" />
					<input type="text" value="搜索">
				</div>
			</header>
			<div class="nav">
				<van-tabs line-width="18px" line-height="4px" title-active-color="#333" @click="getId">
					<van-tab v-for="item in navList" :title="item.name" :key="item._id">
					</van-tab>
				</van-tabs>
			</div>
		</div>
		<List></List>
		<Footer></Footer>
	</div>
</template>

<script>
	import axios from "axios"
	import List from "@/components/List"
	import {
		Tab,
		Tabs,
		Tabbar,
		TabbarItem

	} from 'vant';
	export default {
		components: {
			List,
			[Tab.name]: Tab,
			[Tabs.name]: Tabs,
			[Tabbar.name]: Tabbar,
			[TabbarItem.name]: TabbarItem,
		},
		data() {
			return {
				navList: [],
				active: 0
			}
		},
		methods: {
			getId(index) {
				this.$bus.$emit("cateId", this.navList[index]._id);
			},
			toSearch() {
				this.$router.push('/Search')
			}
		},
		mounted() {
			axios({
				method: 'get',
				url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list`
			}).then(
				res => {
					this.navList = res.data.data;
					// console.table(res.data.data);
				}
			)
		}
	}
</script>

<style scoped lang="less">
	.fixTop {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 90px;
		z-index: 6;
	}

	header {
		width: 100%;
		height: 46px;
		background: #07c160;
		display: flex;
		align-items: center;

		.search {
			position: relative;
			width: 75%;
			height: 32px;
			background: rgba(255, 255, 255, .5);
			margin: 0 auto;
			border-radius: 32px;
			overflow: hidden;

			.van-icon-search {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-150%, -50%);
				font-size: 20px;
			}

			input {
				width: 100%;
				height: 32px;
				border: 0;
				outline: 0;
				background: 0;
				padding-left: 50%;
				color: #fff;
				font-size: 14px;
			}

		}


	}

	.nav {
		width: 100%;
		height: 44px;


		/deep/ .van-tab {
			border-right: 1px solid #ccc;
			border-bottom: 1px solid #ccc;
		}

		/deep/.van-tabs__nav {
			padding: 0;
		}

		/deep/.van-tabs__line {
			bottom: 5px;
			background-color: #07c160;
		}

	}
</style>
