<template>
	<div class="log">
		<div class="enter" v-show="isShow">
			<div class="login">
				<van-cell-group>
					<van-field v-model="phone" left-icon="phone-o" clearable placeholder="请输入手机号"
						@keyup.enter="login" />
					<van-field v-model="pas" type="password" clearable left-icon="bag-o" placeholder="请输入密码"
						@keyup.enter="login" />
				</van-cell-group>
			</div>
			<p class="isLog">还没注册码？先去<span @click="isShow=false;isForget=false"> 注册 </span>吧！<span
					@click="isForget=true;isShow=false">忘记密码</span></p>
			<div class="btn">
				<van-button type="danger" block @click="login">登录</van-button>
			</div>
		</div>
		<!-- 头 -->
		<header :style="{background:'red'}" class="header">
			<van-icon name="arrow-left" @click="isLog" />
			<p>{{isForget?"忘记密码":"注册 / 登录"}}</p>
		</header>
		<!-- 注册 -->
		<div class="register" v-show="!isShow">

			<div class="login">
				<van-cell-group>
					<van-field v-model="logPhone" left-icon="phone-o" clearable placeholder="请输入手机号" />
					<van-field v-model="logPas" type="password" clearable left-icon="bag-o"
						:placeholder="isForget?`请输入新密码 6-12位数字字母下划线`:`请输入密码 6-12位数字字母下划线`" />
					<van-field v-model="sms" center label="短信验证码" placeholder="请输入短信验证码">
						<template #button>
							<van-button size="small" :disabled="disabled" type="primary" @click="sendSms">
								{{disabled?timer:'发送验证码'}}
							</van-button>
						</template>
					</van-field>

				</van-cell-group>
			</div>
			<p class="isLog" v-show="!isForget">注册完成了么？去<span @click="isShow=true"> 登录 </span>吧！<span
					@click="isForget=true">忘记密码</span></p>
			<div class="btn">
				<van-button type="danger" block @click="register">{{isForget?"确定":"注册"}}</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from "axios";
	import {
		Notify,
		Dialog,
		Toast
	} from "vant"
	export default {
		name: 'login',
		data() {
			return {
				phone: '',
				pas: '',
				logPhone: '',
				logPas: '',
				sms: '',
				isShow: true,
				disabled: false,
				timer: 5,
				isForget: true
			}
		},
		watch: {
			isShow(newValue) {
				if (newValue) {
					this.phone = "";
					this.pas = "";
				} else {
					this.logPhone = "";
					this.logPas = "";
					this.sms = "";
				}
			}
		},
		methods: {
			isLog() {
				if (this.isForget) {
					this.isForget = false
					this.isShow = true
					return
				}
				this.$router.push('/Mine')
			},
			back() {
				console.log(1);
			},
			login() {
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/login',
					data: {
						username: this.phone,
						password: this.pas
					}
				}).then(
					res => {
						// console.log(res.data);
						if (!res.data.code) {
							Notify({
								type: 'success',
								message: '登录成功',
								duration: 1500
							});
							this.$router.push({
								path: '/Mine',
							});
							this.$store.commit('aboutPerson/per_info', res.data.userInfo);
						} else if (res.data.code == 90002) {
							Dialog.alert({
								title: '注意',
								message: res.data.msg,
								theme: 'round-button',
							});
						} else {
							Dialog.alert({
								title: '注意',
								message: res.data.msg,
								theme: 'round-button',
							});
						}

					}
				)
			},
			sendSms() {
				this.disabled = true;
				let time = setInterval(() => {
					this.timer--
					if (!this.timer) {
						this.disabled = false;
					}
				}, 1000);
				let regPhone = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
				if (!this.logPhone) {
					Dialog.alert({
						message: '请输入你的手机号!',
					});
					return
				}
				if (!regPhone.test(this.logPhone)) {
					Dialog.alert({
						message: '请输入正确的手机号!',
					});
					clearInterval(time);
					this.disabled = false;
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
					data: {
						mobile: this.logPhone,
						type: this.isForget ? 'login' : 'register'
					}
				}).then(
					res => {
						// console.log(res.data);
						if (res.data.data) {
							setTimeout(() => {
								Notify({
									type: 'success',
									message: `您的验证码为：${res.data.data}`,
									duration: 4000
								});
								clearInterval(time);
								this.disabled = false;
							}, 2000);
						}
						Dialog.alert({
							message: res.data.msg,
						});
					}
				)
			},
			register() {
				axios({
					method: 'post',
					url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/${this.isForget?'forget':'reg'}`,
					data: {
						username: this.logPhone,
						password: this.logPas,
						vercode: this.sms
					}
				}).then(
					res => {
						if (!res.data.code) {
							setTimeout(() => {
								this.isShow = true;
							}, 300);
						}
						Dialog.alert({
							message: res.data.msg,
						});
					}
				)
			}
		},
		mounted() {
			this.isShow = true;
		}
	}
</script>

<style scoped lang="less">
	.log {
		margin-top: 50px;
	}

	.login {
		margin: 10px 0;
	}

	.isLog {
		margin: 20px 0;
		padding: 0 10px;

		span {
			color: red;

			&:last-child {
				float: right;
			}
		}
	}

	.btn {
		margin: 0 auto;
		width: 90%;
		height: 44px;
		border-radius: 44px;
		overflow: hidden;
	}
</style>
