<template>
	<div>
		<div class="person_info" v-if="person_info">
			<div class="info_top">
				<div class="username">
					<img :src="person_info.avatar" alt="">
					<p>{{person_info.nickname}}</p>
				</div>
				<div class="update_info">
					<span @click="updatePersonInfo">编辑资料</span>
				</div>
			</div>
			<div class="info_bottom">
				<div @click="toMyPub('发布')">
					<p>{{myPub.length}}</p>
					<p>头条</p>
				</div>
				<div>
					<p>0</p>
					<p>获赞</p>
				</div>
			</div>
		</div>
		<div v-if="!person_info" class="noLogin">
			<h2>登录后更懂你</h2>
			<van-button type="danger" style="border-radius: 5px;" @click="toLogin">立即登录</van-button>
		</div>
		<van-grid clickable :column-num="2">
			<van-grid-item icon="star-o" @click="toMyPub('收藏')" text="收藏" />
			<van-grid-item icon="underway-o" @click="toMyPub('历史')" text="历史" />
		</van-grid>
		<div class="operate">
			<van-cell title="修改密码" is-link @click="showTrue" />
			<van-cell title="联系我们" is-link @click="callMe" />
			<van-cell title="关于我们" is-link @click="aboutMe" />
		</div>
		<van-button block @click="logOut">退出登录</van-button>
		<Footer></Footer>
		<van-popup v-model="show">
			<van-field v-model="oldPas" placeholder="请输入你的旧密码" />
			<van-field v-model="newPas" placeholder="请输入你的新密码" />
			<div class="btns">
				<van-button type="default" size="small" @click="noShow">取消</van-button>
				<van-button type="primary" size="small" @click="changePas">确认</van-button>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Grid,
		GridItem,
		Dialog,
		Toast,
		Empty
	} from 'vant';
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			[Grid.name]: Grid,
			[GridItem.name]: GridItem,
			[Empty.name]: Empty,
		},
		data() {
			return {
				show: false,
				oldPas: '',
				newPas: '',
				myPub: []
			}
		},
		methods: {
			toLogin() {
				this.$router.push('/Login')
			},
			updatePersonInfo() {
				this.$router.push('/updateInfo')
			},
			logOut() {
				if (this.person_info) {
					Dialog.confirm({
							message: '确认退出登录吗?',
						})
						.then(() => {
							// on confirm
							setTimeout(() => {
								this.$store.commit('aboutPerson/per_info', null);
								localStorage.removeItem('personInfo');
								Toast.success('退出登录成功');
							}, 200)
						})
						.catch(() => {
							// on cancel
						});
				} else {
					Toast.fail('您还未登录哦');
				}
			},
			showTrue() {
				if (this.person_info) {
					this.show = true;
				} else {
					Toast.fail('您还未登录哦');
				}
			},
			callMe() {
				Dialog.alert({
					title: '联系方式',
					message: '手机号：18539839614<br>邮箱：chengchaofan0329@foxmail.com<br>微信：cf831143',
					messageAlign: 'left'
				}).then(() => {
					// on close
				});
			},
			aboutMe() {
				Dialog.alert({
					title: '关于我们',
				})
			},
			noShow() {
				this.show = false;
				this.oldPas = "";
				this.newPas = "";
			},
			changePas() {
				if (!this.oldPas) {
					Dialog.alert({
						message: '请输入你的旧密码!',
					});
					return
				}
				if (!this.newPas) {
					Dialog.alert({
						message: '请输入你的新密码!',
					});
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd',
					data: {
						oldPassword: this.oldPas,
						newPassword: this.newPas,
						uid: this.person_info._id
					}
				}).then(
					res => {
						console.log(res.data);
						// console.log(this.person_info);
						if (!res.data.code) {
							Toast.success(`${res.data.msg}请重新登录`);
							this.$store.commit('aboutPerson/per_info', null);
							localStorage.removeItem('personInfo');
							this.show = false;
							this.oldPas = "";
							this.newPas = "";
						} else {
							Toast.fail(`${res.data.msg}`);
						}
					}
				)
			},
			toMyPub(info) {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				this.$router.push({
					path: '/myPublish',
					query: {
						info
					}
				});
			}
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		activated() {
			if (this.person_info) {
				axios({
					method: 'get',
					url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.person_info._id}&skip&limit`
				}).then(
					res => {
						this.myPub = res.data.data;
					}
				)
			}
		}
	}
</script>

<style scoped lang="less">
	.person_info {
		width: 100%;
		height: 180px;
		background: #349f37;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-end;
		align-content: center;
		padding: 0 15px;

		div {
			width: 100%;
			height: 70px;
			// background: red;
		}

		.info_top {
			display: flex;

			.username {

				img {
					float: left;
					width: 66px;
					height: 66px;
					border-radius: 50%;
					background: #fff;
					display: inline-block;
					font-size: 14px;
				}

				p {
					padding-top: 15px;
					width: 70%;
					float: right;
					padding-left: 10px;
				}

				color: #fff;
			}

			.update_info {
				width: 40%;
				text-align: right;
				line-height: 66px;

				span {
					font-size: 14px;
					background: #fff;
					padding: 3px 14px;
					border-radius: 26px;
					color: #666;
				}
			}
		}

		.info_bottom {
			text-align: center;
			display: flex;
			justify-content: space-around;
			color: #fff;
			font-size: 14px;

			div {
				width: 30%;
				display: flex;
				flex-direction: column;
				justify-content: center;
			}
		}
	}

	.noLogin {
		width: 100%;
		height: 180px;
		background: #e3e3e3;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		h2 {
			font-weight: 400;
			margin-bottom: 10px;
		}
	}

	.operate {
		margin-top: 5px;
	}

	.van-popup {
		width: 80%;
		padding: 20px;
	}

	.btns {
		display: flex;
		justify-content: space-around;
		margin-top: 10px;
	}

	/deep/ .van-icon-star-o:before {
		color: #f19ca4;
	}

	/deep/ .van-icon-underway-o:before {
		color: #ffad59;
	}

	/deep/ .van-button--block {
		margin-top: 5px;
		color: #e1818f;
	}
</style>
