<template>
	<div>
		<header :style="{background:mainColor}">
			发布
		</header>
		<van-cell-group>
			<van-field v-model.trim="value" ref="ipt" placeholder="请输入标题" />
		</van-cell-group>
		<div class="popo">
			<van-popover v-model="showPopover" trigger="click" :actions="actions" @select="onSelect">
				<template #reference>
					<van-button type="primary">{{title?title:'请选择你要发布的地方'}}</van-button>
				</template>
			</van-popover>
		</div>
		<van-field v-model.trim="message" rows="4" autosize type="textarea" placeholder="请输入内容" />
		<van-uploader :max-count="1" v-model="uploader" />
		<div class="btn">
			<van-button type="primary" size="large" @click="sendInfo">确定</van-button>
		</div>
		<Footer></Footer>
	</div>
</template>

<script>
	import axios from 'axios'
	import {
		nanoid
	} from 'nanoid';
	import {
		Form,
		Uploader,
		Popover,
		Dialog

	} from 'vant';
	import {
		mapState
	} from 'vuex';
	export default {
		data() {
			return {
				value: '',
				message: '',
				uploader: [],
				// columns: ['二手市场', '婚恋交友', '装修装饰', '明日头条', '今日头条', '劳力市场'],
				title: '',
				showPopover: false,
				actions: [],
				cate_id: ''
			}
		},
		components: {
			[Form.name]: Form,
			[Uploader.name]: Uploader,
			[Popover.name]: Popover,
		},
		methods: {
			onSelect(action) {
				this.title = action.text;
			},
			sendInfo() {
				if (!this.value) {
					Dialog.alert({
						message: '请输入你的标题!',
					});
					return
				}
				if (!this.message) {
					Dialog.alert({
						message: '请输入你要发布的内容!',
					});
					return
				}
				if (this.title) {
					axios({
						method: 'get',
						url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list`
					}).then(
						res => {
							let obj = res.data.data.find(val => val.name === this.title);
							this.cate_id = obj._id;
						}
					)
				} else {
					Dialog.alert({
						message: '请选择你要发布的地方!',
					});
					return
				}
				axios({
					method: 'get',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token'
				}).then(
					res => {
						// console.log(res.data.token);
						let FD = new FormData();
						FD.append('key', nanoid());
						FD.append('token', res.data.token)
						FD.append('file', this.uploader[0].file)

						axios({
							method: 'post',
							url: 'https://upload-z1.qiniup.com',
							data: FD
						}).then(
							res => {
								// console.log(res.data);
								axios({
									url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article',
									method: 'post',
									data: {
										"title": this.value,
										"content": this.message,
										"cate_id": this.cate_id,
										"cate_name": this.title,
										"author": this.person_info.username,
										"author_id": this.person_info._id,
										"imageSrc": [`http://toutiao.longxiaokj.com/${res.data.key}`]
									}
								}).then(
									res => {
										console.log(res.data);
										Dialog.alert({
											message: res.data.msg,
										});
										if (res.data.msg == '成功') {
											this.value = '';
											this.message = '';
											this.title = '';
											this.uploader = [];
										}
									}
								)
							}
						)
					}
				)
			}
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		activated() {
			if (!this.person_info) {
				this.$router.push({
					path: '/Login'
				});
				return
			}
			axios({
				method: 'get',
				url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list`
			}).then(
				res => {
					// let obj = res.data.data.find(val => val.name === this.title);
					// this.cate_id = obj._id;
					res.data.data.map(val => {
						this.actions.push({
							text: val.name
						})
					})
				}
			)
		}
	}
</script>

<style scoped lang="less">
	header {
		width: 100%;
		height: 44px;
		text-align: center;
		line-height: 44px;
		color: #fff;
	}

	/deep/ .van-cell {
		margin-top: 10px;
	}

	.popo {
		text-align: center;
		margin: 10px 0;
	}

	.btn {
		width: 80%;
		margin: 0 auto;
		margin-bottom: 60px;
	}
</style>
