<template>
	<div>
		<div class="search">
			<van-field v-model="search" ref="ipt" clearable @input="fuzzySearch" left-icon="search"
				placeholder="请输入搜索关键词(无痕浏览)" />
			<span @click="$router.back()">取消</span>
		</div>
		<ul class="list commonList">
			<li v-for="item in searchList" :key="item._id" @click="toDetail(item)">
				<div class="list_top">
					<p>{{item.title}}</p>
					<div v-show="item.imageSrc">
						<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" :key="i">
					</div>
				</div>
				<div class="list_bottom">
					<span>作者 {{item.author}}</span>
					<span>{{item.comment}} 评论</span>
					<span>发布日期：{{new Date(item.time).toLocaleDateString()}}
						{{new Date(item.time).toLocaleTimeString()}}</span>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Toast
	} from 'vant';
	export default {
		name: 'search',
		data() {
			return {
				search: '',
				isSend: false,
				searchList: []
			}
		},
		methods: {
			fuzzySearch() {
				if (!this.search) {
					this.searchList = [];
					console.log(1);
					return
				}
				if (this.isSend) return
				this.isSend = true
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',
					data: {
						key_word: this.search
					}
				}).then(
					res => {
						this.searchList = res.data.data;
						this.isSend = false
					}
				)
			}
		},
		activated() {
			this.$refs.ipt.focus();
			this.search = '';
			this.searchList = [];
		},
		mounted() {
			Toast.success('你已进入无痕浏览');
		}
	}
</script>

<style scoped lang="less">
	.search {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		padding: 15px;
		background: #fff;
		display: flex;
		align-items: center;
		justify-content: space-between;

		.van-cell {
			background: #f5f7f9;
			padding: 4px 10px;
			width: 90%;

		}

		span {
			font-size: 14px;
		}
	}
</style>
