import VueRouter from 'vue-router'
import navRouter from '@/router/nav'
import Detail from "@/routePage/Detail"
import Login from "@/routePage/Login"
import updateInfo from "@/routePage/updateInfo"
import myPublish from "@/routePage/myPublish"
import Search from "@/routePage/Search"
export default new VueRouter({
	mode: 'history',
	routes: [{
			path: '/Detail',
			component: Detail,
			props(route) {
				return {
					item: route.query
				}
			}
		}, {
			path: '/Login',
			component: Login,
		}, {
			path: '/updateInfo',
			component: updateInfo,
		}, {
			path: '/myPublish',
			component: myPublish,
			props(route) {
				return {
					info: route.query.info
				}
			}
		},
		{
			path: '/Search',
			component: Search
		},
		...navRouter
	]
})
